import React, { FC } from 'react';
import { PolygonProps } from './Polygon.types';
import { draw } from '../../svg/svg.methods';
import { add, ngon, rad } from '../../geometry';

export const Polygon: FC<PolygonProps> = ({
	sides = 6,
	rotation = 0,
	scale = 1,
	cornerRadius = 0,
	fillColor = '#000',
	strokeColor = '#000',
	lineWidth = 1,
}) => {
	const [points, viewBox] = ngon(sides, scale, rad(rotation), cornerRadius, [0, 0]);
	const hl = lineWidth > 0 ? lineWidth/2:0;
	const newViewBox = add(viewBox, [-hl, -hl, lineWidth, lineWidth]);
	return (<svg xmlns="http://www.w3.org/2000/svg" viewBox={newViewBox.join(' ')} width="100px" height="100px">
		<path d={draw(points)} fill={fillColor} stroke={strokeColor} strokeWidth={lineWidth}/>
	</svg>)
}